const someArray = [1, 2, 3];

const Table = () => (
  <table>
    <tr>
      <th></th>
      <th></th>
      <th></th>
      <th></th>
      <th></th>
      <th></th>
    </tr>
    {Object.values(someArray).map((value, index) => {
      return (
        <tr key={index}>
          <td>{value}</td>
          <td>{value}</td>
          <td>{value}</td>
          <td>{value}</td>
          <td>{value}</td>
          <td>{value}</td>
        </tr>
      );
    })}
  </table>
);

export default Table;
